.footer {
  background-color: whitesmoke;
  width: auto;
  display: center;
  align-items: center;
  padding-top: 3em;
  position: sticky;
  z-index: 100;
  height: 65px;
}

.footer__logo {
  width: 200px;
  height: 400px;
  margin: 0 10px;
  margin-top: 10px;
  object-fit: contain;
}

.footer__search {
  display: flex;
  flex: 1;
  color: white;
}

.footer__searchInput {
  height: 15px;
  width: 200px;
  padding: 10px;
  background-color: whitesmoke;
  border-radius: 10px;
  border-width: 3px;
  border-color: rgb(6, 241, 77);
}

.footer__nav {
  display: flex;
  justify-content: space-evenly;
}

.footer__link {
  color: white;
  text-decoration: none;
}

.footer__option {
  align-items: center;
  color: black;
}

.footer__optionLineTwo {
  font-size: 15px;
  font-weight: 200;
  color: black;
}
